/*------------------------------------*		#BASE
\*------------------------------------*/
/**
 * 1. Make every element inherit the value for the box-sizing property. This 
 * 		allows for easy overriding and cascading.
 */
*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit; }

/**
 * 1. Auto-hiding scrollbars are used if the element's content overflows
 *		(IE 10/11).
 * 2. Disable unwanted auto-enlargement of small text in mobile browsers.
 * 3. Set to border-box so the width/height of an element is calculated to
 *		include padding.
 * 4. Set root font-size to 0.625rem that 1rem == 10px.
 */
:root {
  -ms-overflow-style: -ms-autohiding-scrollbar;
  text-size-adjust: 100%;
  box-sizing: border-box;
  font-size: 0.625rem;
  font-family: "Helvetica Neue", "Source Sans Pro", "Arial", sans-serif;
  font-weight: 300;
  line-height: 1.618;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  cursor: default; }

/**
 * 1. Hide auto element in Chrome 40+, iOS 8+, Safari 8+.
 */
audio:not([controls]) {
  display: none; }

details {
  display: block; }

input[type="number"] {
  width: auto; }

input[type="search"] {
  -webkit-appearance: textfield; }
  input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none; }

main {
  display: block; }

pre {
  overflow: auto; }

textarea {
  overflow: auto; }

[hidden] {
  display: none; }

[unselectable] {
  user-select: none; }

a {
  text-decoration: none; }

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle; }

button,
input,
select,
textarea {
  color: inherit;
  font-family: inherit;
  font-style: inherit;
  font-weight: inherit; }

code,
kbd,
pre,
samp {
  font-family: monospace; }

/*------------------------------------*    #TYPOGRAPHY
\*------------------------------------*/
body {
  font-size: 1.618rem;
  color: #212121; }

p {
  margin: 0 0 2.61792rem; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-weight: 300;
  font-family: "Source Sans Pro", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  color: #212121; }

h1 {
  font-size: 5.545rem;
  margin-bottom: 2.61792rem; }

h2 {
  font-size: 4.236rem;
  margin-bottom: 2.15644rem; }

h3 {
  font-size: 3.427rem;
  margin-bottom: 1.618rem; }

h4 {
  font-size: 2.618rem;
  margin-bottom: 1.21877rem; }

h5 {
  font-size: 2.148rem;
  margin-bottom: 0.99997rem; }

h6 {
  font-size: 1.618rem;
  margin-bottom: 0.76391rem; }

small {
  font-size: 75%; }

.underline {
  text-decoration: underline; }

/*------------------------------------*		#GRID
\*------------------------------------*/
.c-grid {
  display: block;
  width: 100%; }
  .c-grid__col {
    flex: 1; }
    .c-grid__col--1 {
      flex: 1; }
    .c-grid__col--2 {
      flex: 2; }
    .c-grid__col--3 {
      flex: 3; }
    .c-grid__col--4 {
      flex: 4; }
    .c-grid__col--5 {
      flex: 5; }
    .c-grid__col--6 {
      flex: 6; }
  @media (min-width: 768px) {
    .c-grid {
      display: flex;
      flex-wrap: no-wrap; }
      .c-grid__col {
        margin-left: 1.00689rem; }
        .c-grid__col:first-child {
          margin-left: 0; } }

/*------------------------------------*		#LIST
\*------------------------------------*/
.c-list {
  margin: 0 0 2.61792rem; }
  .c-list .c-list {
    margin: 0 0 0 2.61792rem; }
  .c-list-item {
    margin-bottom: 0.65448rem; }
  .c-list--clean {
    list-style-type: none; }

/*------------------------------------*    #FORM
\*------------------------------------*/
.c-form {
  margin: 0 0 2.61792rem; }

.c-fieldset {
  border: none;
  margin: 0 0 1.30896rem; }

.c-label {
  display: block;
  margin-bottom: 0.43632rem; }

.c-input {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 3.92689rem;
  margin: 0 0 0.65448rem;
  padding: 0.65448rem 0.87264rem;
  border-radius: 5px;
  vertical-align: middle;
  border: 1px solid #BDBDBD;
  font: inherit;
  color: inherit;
  background: white; }
  .c-input:focus {
    outline: none;
    box-shadow: 0 0 4px 1px grey; }

.c-textarea {
  height: auto;
  min-height: 3.92689rem; }

.c-input-option {
  display: inline-block;
  margin-right: 0.43632rem;
  padding: 0;
  height: auto; }
  .c-input-option:focus {
    outline: none; }

/*------------------------------------*		#BUTTON
\*------------------------------------*/
.c-button {
  appearance: none;
  border: none;
  display: inline-block;
  margin: 0 0.43632rem 2.61792rem 0;
  padding: 0.87264rem 2.61792rem;
  border-radius: 5px;
  vertical-align: middle;
  white-space: nowrap;
  line-height: inherit;
  font-size: 1.618rem;
  cursor: pointer;
  color: #212121;
  background: #F1F1F1; }
  .c-button:focus {
    outline: none; }

/*------------------------------------*		#TABLE
\*------------------------------------*/
.c-table {
  box-sizing: border-box;
  width: 100%;
  margin: 0 0 2.61792rem;
  border-collapse: collapse; }

.c-th,
.c-td {
  padding: 1.30896rem; }

.c-th {
  text-align: left; }

.c-tr:nth-child(odd) .c-td {
  background: #F1F1F1; }

/*------------------------------------*		#CODE
\*------------------------------------*/
pre {
  display: block; }

pre > code {
  display: block;
  margin-bottom: 2.61792rem;
  white-space: pre;
  padding: 1.74528rem;
  font-size: 1.25rem; }

code {
  padding: 0.43632rem;
  border-radius: 5px;
  word-wrap: break-word;
  background: #F1F1F1;
  font-size: 1.618rem;
  color: #212121; }

